@font-face {
  font-family: "PT Mono";
  src: url("../fonts/ptmono.woff") format("woff"), url("../fonts/ptmono.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Roboto Condensed";
  src: url("../fonts/Robotocondensed.woff") format("woff"), url("../fonts/Robotocondensed.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Roboto Condensed";
  src: url("../fonts/Robotocondensedbold.woff") format("woff"), url("../fonts/Robotocondensedbold.woff2") format("woff2");
  font-weight: 700;
  font-style: normal;
  font-display: swap; }

html {
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

*,
*::before,
*::after {
  -webkit-box-sizing: inherit;
          box-sizing: inherit; }

body {
  min-width: 320px;
  max-width: 1920px;
  font-family: "PT Mono", "Courier", monospace;
  font-size: 16px;
  line-height: 22px;
  font-weight: 400;
  color: #666666;
  font-style: normal;
  background-color: #ffffff; }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.wrapper {
  width: 100%; }

.container {
  margin: 0 auto;
  max-width: 74%; }

.logo {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
  width: 250px;
  margin-left: 6px;
  margin-top: 3px; }

.logo img {
  display: block;
  width: 52px;
  height: 52px; }

.logo__description {
  display: block;
  margin: 0;
  margin-left: 16px;
  padding-top: 10px; }

.logo__description-item {
  display: block;
  margin-bottom: 10px;
  font-size: 0;
  line-height: 0; }

.page-header {
  padding-top: 30px;
  background-color: #1B2D37; }
